<template>
  <div class="Tab">
    <ul>
      <li v-for="(item , index) in routerlist" :key="index" @click="goTab(item.path)">
        <i :class="$route.path==item.path ? item.active :item.icon"></i>
        <span :class="$route.path==item.path? 'active' : '' ">{{item.title}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {
    }
  },
  created () {
    this.$store.dispatch('getlist')
  },
  methods: {
    // ...mapMutations(['initlist']),
    goTab (path) {
      if (this.$route.path === path) return
      this.$router.replace(path)
    }
  },
  computed: {
    ...mapState(['routerlist'])
  }
}
</script>

<style lang="less" scoped>
.Tab {
  width: 100%;
  height: 50px;
  background-color: #fff;
  position: fixed;
  left: 0;
  bottom: 0;
  ul {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    li {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
}
.active{
  color: rgb(34, 75, 243);
}
</style>
