<template>
  <div class="head">
    <span><b>{{ShowHead()}}</b></span>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {}
  },
  computed: {
    ...mapState(['routerlist'])
  },
  methods: {
    ShowHead () {
      const path = this.routerlist
      // path.forEach(item => {
      //   if (this.$route.path === item.path) {
      //     console.log(item.title)
      //     return item.title
      //   }
      // })
      for (let i = 0; i < path.length; i++) {
        if (this.$route.path === path[i].path) {
          return path[i].head
        }
      }
    }
  }
}
</script>
<style lang="less" scoped>
.head{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: rgb(60, 120, 239);
  color:#fff;
  text-align: center;
  line-height: 50px;
}
</style>
