import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    routerlist: []
  },
  getters: {
  },
  mutations: {
    // 将axios请求到的routerlist数据放到state.routerlist下
    initlist (state, list) {
      state.routerlist = list
    }
  },
  actions: {
    getlist (context) {
      // 请求public目录下的json文件
      axios.get('./routerlist.json').then(({ data }) => {
        // 调用initlist方法
        context.commit('initlist', data)
      })
    }
  },
  modules: {
  }
})
